/* footer Style */
.footer--default {
    @apply bg-primary flex flex-col items-center justify-center shadow-2xl rounded-t-[50px];
}

.footer--phone {
    @apply h-[70px] leading-[22px];
}

.footer--tablet {
    @apply lg:h-[100px] lg:leading-[25px];
}

.footer--desktop {
    @apply xl:h-[100px] xl:leading-[28px];
}

/* content Style */
.content--default {
    @apply items-center justify-center flex gap-4;
}

.content--phone {
    @apply h-[90px] leading-[22px];
}

.content--tablet {
    @apply lg:h-[100px] lg:leading-[25px];
}

.content--desktop {
    @apply xl:h-[100px] xl:leading-[28px];
}

/* media Style */
.media--default {
    @apply flex items-center;
}

/* media image Style */
.media--image--default {
    @apply w-[30px];
}

.media--image--phone {
    @apply w-[30px];
}

.media--image--tablet {
    @apply max-w-[80%];
}

.media--image--desktop {
    @apply xl:w-[40px] xl:mr-1;
}

/* media text Style */
.media--text--default {
    @apply text-white font-poppins font-bold;
}

.media--text--phone {
    @apply text-[12px];
}

.media--text--tablet {
    @apply lg:text-[18px];
}

.media--text--desktop {
    @apply xl:text-[22px];
}