/* header Style */
.header--default {
    @apply bg-primary flex flex-col items-center justify-center shadow-2xl rounded-b-[50px];
}

.header--phone {
    @apply h-[110px] leading-[30px];
}

.header--tablet {
    @apply lg:h-[100px] lg:leading-[25px];
}

.header--desktop {
    @apply xl:h-[100px] xl:leading-[28px];
}

/* h1 Style */
.h1--default {
    @apply text-white font-poppins font-bold;
}

.h1--phone {
    @apply text-[30px];
}

.h1--tablet {
    @apply lg:text-[25px];
}

.h1--desktop {
    @apply xl:text-[28px];
}

/* p Style */
.p--default {
    @apply text-white font-poppins;
}

.p--phone {
    @apply text-[13px];
}

.p--tablet {
    @apply lg:text-[15px];
}

.p--desktop {
    @apply xl:text-[18px];
}