/* body Style */
.body--default {
    @apply flex items-center justify-center flex-grow
}

/* must symbol Style */
.must--symbol {
    @apply text-red-600
}

/* background card box Style */
.background--card--box--default {
    @apply rounded-[20px] shadow-3xl bg-cover bg-center
}

.background--card--box--phone {
    @apply w-[85%] h-auto
}

.background--card--box--tablet {
    @apply lg:w-[60%] lg:h-auto
}

.background--card--box--desktop {
    @apply xl:w-[35%] xl:h-auto
}

/* card box Style */
.card--box--default {
    @apply w-full h-full backdrop-blur-[10px] rounded-[20px] flex flex-col items-center justify-center
}

.card--box--phone {
    @apply p-5
}

.card--box--tablet {
    @apply lg:p-8
}

.card--box--desktop {
    @apply xl:px-10 xl:py-6
}

/* name container Style */
.input--default {
    @apply w-full my-2
}

/* text input Style */
.text--input--default {
    @apply text-white font-poppins font-bold text-[16px] text-start tracking-wide
}

.text--input--phone {}

.text--input--tablet {}

.text--input--desktop {}

/* input name Style */
.input--name--default {
    @apply w-[100%] h-9 rounded-xl shadow-xl px-2 text-primary font-poppins font-semibold
}

.input--name--phone {}

.input--name--tablet {}

.input--name--desktop {}

/* input date Style */
.input--date--default {
    @apply w-[100%] h-9 rounded-xl shadow-xl px-2 text-primary font-poppins font-semibold
}

.input--date--phone {}

.input--date--tablet {}

.input--date--desktop {}

/* button submit Style */
.button--submit--default {
    @apply w-[100%] h-9 mt-3 text-white font-poppins font-bold shadow-button rounded-full bg-primary mb-5
}

.button--submit--phone {}

.button--submit--tablet {}

.button--submit--desktop {}

/* result container style Style */
.result--default {
    @apply flex flex-col items-center
}

/* params result Style */
.params--result--default {
    @apply text-white font-poppins font-semibold text-[16px]
}

.params--result--desktop {}

.background--card--box--default {}

.params--result--phone {}

/* h1 result Style */
.h1--result--default {
    @apply text-white font-poppins font-bold text-[32px] text-center
}

.h1--result--desktop {}

.background--card--box--default {}

